:root {
  
  /* Main Settings  */
  --main-background-color: rgb(164, 216, 223);

  --text-primary-color: rgb(10, 41, 128);
  --text-primary-font-size: 16px;
  --text-secondary-color: rgb(7, 40, 102);

  /* Nav Settings  */
  --nav-link-color: rgb(6, 75, 177);
  --nav-link-hover-color: rgb(64, 39, 175);
  --nav-link-hover-background-color: none;
  --nav-background-color: none;
  --nav-height: 150px;
  --nav-logo-width: 125px;
  --nav-logo-font-size: 16px;

  /* Mobile Nav Settings  */

  --mobile-nav-background-color: rgb(155, 155, 155);
  --mobile-nav-logo-height:100px;
  --mobile-nav-link-color: rgb(65, 65, 65);
  --mobile-nav-link-hover-color: rgb(68, 77, 160);
  --mobile-nav-link-size: 20px;

  /* Card Settings  */
  --card-background-color: rgb(152, 200, 219);
  --card-border-radius: 10px;
  --card-shadow: 10px 10px 20px -13px rgb(148, 147, 147);

  /* Minting Area */
  --minting-box-color: #78a2c9;
  --minting-box-heading-color: rgb(31, 94, 153);
  --minting-box-info-box-color: rgb(35, 104, 161);
  --minting-box-text-color: rgb(22, 35, 145);


  /* Minting Button */
  --minting-button-color: linear-gradient(180deg,#4a80e5 0%,#4a80e5 100%);
  --minting-button-text-color: rgb(35, 75, 161);

}






